/* Custom styles for Swiper navigation buttons */
.swiper-button-next,
.swiper-button-prev {
  opacity: 0 !important;
  visibility: hidden !important;
  transform: scale(0.8);
  transition: opacity 0.3s ease, visibility 0.3s ease, transform 0.3s ease;
}

.product-list-slider:hover .swiper-button-next,
.product-list-slider:hover .swiper-button-prev {
  opacity: 1 !important;
  visibility: visible !important;
  transform: scale(1);
}

/* Override any default Swiper styles */
.swiper-button-disabled {
  opacity: 0.5 !important;
}
